@import "./reset.scss";

$bg: rgb(255, 246, 224);
$bg-font: rgb(255, 228, 158);
$primary: rgb(247, 203, 138);


:root {
    font-family: 'Courier New', Courier, monospace;
}

body {
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.app {
    background-color: $bg;

    >div {
        height: 100vh;
        position: relative;
    }

    .watermark {
        z-index: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0.5;
        color: $bg-font;
        font-size: 15vw;
    }

    .main {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;

            >div {
                margin: 0px auto;
                padding: 20px;
                transition: transform 3s ease;
            }

            .left {}

            .right {}
        }
    }

    .skills {
        display: flex;
        align-items: center;
        justify-content: center;

        .container {
            z-index: 1;
            width: 80vw;
            display: flex;
            align-items: center;
            justify-content: center;
            shape-outside: radial-gradient(farthest-side at right, transparent calc(100% - var(--p)), #fff 0);


            .skills {
                width: 50vw;
                display: flex;
                flex-wrap: wrap;

                span {
                    margin: 10px;
                }
            }
        }
    }

    .portfolio {}
}